pfe-navigation-item [slot="trigger"] a,
.pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a,
.pfe-navigation-item__tray--container a, pfe-navigation .pfe-link-link--header > a {
  color: var(--pfe-broadcasted--color--ui-link, #06c);
  text-decoration: none;
}

pfe-navigation-item [slot="trigger"] a:visited,
.pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a:visited,
.pfe-navigation-item__tray--container a:visited, pfe-navigation .pfe-link-link--header > a:visited {
  color: var(--pfe-broadcasted--color--ui-link--visited, rebeccapurple);
}

pfe-navigation-item [slot="trigger"] a:hover,
.pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a:hover,
.pfe-navigation-item__tray--container a:hover, pfe-navigation .pfe-link-link--header > a:hover {
  color: var(--pfe-broadcasted--color--ui-link--hover, #003366);
  text-decoration: none;
}

pfe-navigation-item [slot="trigger"] a:focus,
.pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a:focus,
.pfe-navigation-item__tray--container a:focus, pfe-navigation .pfe-link-link--header > a:focus {
  outline: none;
  color: var(--pfe-broadcasted--color--ui-link--focus, #003366);
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .expanded .pfe-navigation-item__trigger.pfe-navigation-item {
    background-color: var(--pfe-theme--color--surface--lightest, #fff);
    border-top-color: var(--pfe-theme--color--surface--accent, #fe460d);
  }
  .pfe-navigation-item__trigger.pfe-navigation-item:hover {
    cursor: pointer;
    border-top: var(--pfe-theme--surface--border-width--heavy, 4px) solid var(--pfe-theme--color--surface--accent, #fe460d);
  }
  .pfe-navigation__utility--mobile > *:not(:last-child) {
    margin-bottom: var(--pfe-theme--container-spacer, 16px);
  }
  .pfe-navigation-item__tray--container {
    padding-top: var(--pfe-theme--container-spacer, 16px);
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .pfe-navigation-item__trigger > *.pfe-navigation-item__trigger--icon:first-child {
    display: none;
  }
}

.pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) {
  margin: 0;
  font-family: var(--pfe-theme--font-family, "Overpass", Overpass, Helvetica, helvetica, arial, sans-serif);
  font-size: var(--pfe-navigation-item__trigger--FontSize, 14px);
  font-weight: var(--pfe-navigation-item__trigger--FontWeight, 100);
  color: var(--pfe-navigation-item--Color, #fff);
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) {
    color: var(--pfe-navigation-item--Color, #fff);
  }
  .expanded .pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) {
    color: var(--pfe-theme--color--text, #333);
  }
  .pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a,
  .pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a:hover,
  .pfe-navigation-item__trigger > *:not(.pfe-navigation-item__trigger--icon) a:visited {
    color: inherit;
  }
}

pfe-navigation [slot="logo"] > a {
  color: var(--pfe-navigation-item--Color, #fff);
  text-decoration: none;
}

pfe-navigation [slot="logo"] > a:focus {
  outline: var(--pfe-theme--surface--border-width, 1px) dashed var(--pfe-theme--color--surface--lightest, #fff);
}

.pfe-navigation-item__tray {
  padding: var(--pfe-navigation-item__tray--Padding, 16px);
  max-height: calc(100vh - var(--pfe-navigation--Height, 90px));
  overflow-x: hidden;
  overflow-y: scroll;
}

.pfe-navigation-item__tray--container {
  max-width: var(--pfe-navigation--Width);
  margin: 0 auto;
}
